	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is Python?</h2>\n<p><a href='https://www.python.org' target='_blank' rel='nofollow'>Python</a> is a general purpose programming language which is dynamically typed, interpreted, and known for its easy readability with great design principles.</p>\n<p>To know more about Python, you might want to check <a href='https://www.python.org/doc/essays/blurb/' target='_blank' rel='nofollow'>this</a> and <a href='https://docs.python.org/3/faq/general.html' target='_blank' rel='nofollow'>this</a>.</p>\n<h2>Python 2 or Python 3</h2>\n<ul>\n<li>The two versions are similar, with knowledge of one switching to writing code for the other is easy.</li>\n<li>\n<p><a href='https://wiki.python.org/moin/Python2orPython3' target='_blank' rel='nofollow'>Python 2 or Python 3</a></p>\n<ul>\n<li><a href='https://www.python.org/dev/peps/pep-0373/' target='_blank' rel='nofollow'>Python 2.x will not be maintained past 2020.</a> </li>\n<li>3.x is under active development. This means that all recent standard library improvements, for example, are only available by default in Python 3.x.</li>\n<li>Python ecosystem has amassed a significant amount of quality software over the years. The downside of breaking backwards compatibility in 3.x is that some of that software (especially in-house software in companies) still doesn’t work on 3.x yet.</li>\n</ul>\n</li>\n</ul>\n<h2>Installation</h2>\n<p>Most *nix based operating systems come with Python installed (usually Python 2). Replacing the system Python is not recommended and may cause problems. However, different versions of Python can be safely installed along side the system Python. See <a href='https://docs.python.org/3/using/index.html' target='_blank' rel='nofollow'>Python Setup and Usage</a></p>\n<p>Windows doesn’t come with Python, the installer and instructions can be found <a href='https://docs.python.org/3/using/windows.html' target='_blank' rel='nofollow'>here</a></p>\n<h2>Python Interpreter</h2>\n<p>The Python interpreter is what is used to run Python scripts.</p>\n<p>If it is available and in Unix shell’s search path makes it possible to start it by typing the command <code>python</code> followed by the script name will invoke the interpreter and run the script.</p>\n<p><code>hello_campers.py</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-python\"><code><span class=\"token keyword\">print</span><span class=\"token punctuation\">(</span><span class=\"token string\">'Hello campers!'</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p>From terminal:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ python hello_campers.py\nHello campers!</code></pre>\n      </div>\n<p>“When multiple versions of Python are installed, calling them by version is possible depending on the install configuration. In the Cloud9 ide custom environment, they can be invoked like:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ python --version\nPython 2.7.6\n$ python3 --version\nPython 3.4.3\n$ python3.5 --version\nPython 3.5.1\n$ python3.6 --version\nPython 3.6.2 </code></pre>\n      </div>\n<h2>Python Interpreter Interactive Mode</h2>\n<p>Interactive mode can be started by invoking the Python interpreter with the <code>-i</code> flag or without any arguments.</p>\n<p>Interactive mode has a prompt where Python commands can be entered and run:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ python3.5\nPython 3.5.1 (default, Dec 18 2015, 00:00:00)\nGCC 4.8.4 on linux\nType \"help\", \"copyright\", \"credits\" or \"license\" for more information.\n>>> print(\"Hello campers!\")\nHello campers!\n>>> 1 + 2\n3\n>>> exit()\n$</code></pre>\n      </div>\n<h2>The Zen of Python</h2>\n<p>Some of the principles that influenced the design of Python are included as an easter egg and can be read by using the command inside Python interpreter interactive mode:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>>>> import this\nThe Zen of Python, by Tim Peters\n\nBeautiful is better than ugly.\nExplicit is better than implicit.\nSimple is better than complex.\nComplex is better than complicated.\nFlat is better than nested.\nSparse is better than dense.\nReadability counts.\nSpecial cases aren't special enough to break the rules.\nAlthough practicality beats purity.\nErrors should never pass silently.\nUnless explicitly silenced.\nIn the face of ambiguity, refuse the temptation to guess.\nThere should be one-- and preferably only one --obvious way to do it.\nAlthough that way may not be obvious at first unless you're Dutch.\nNow is better than never.\nAlthough never is often better than *right* now.\nIf the implementation is hard to explain, it's a bad idea.\nIf the implementation is easy to explain, it may be a good idea.\nNamespaces are one honking great idea -- let's do more of those!</code></pre>\n      </div>\n<h2>Pros and Cons of Python</h2>\n<h3>Pros</h3>\n<ol>\n<li>Interactive language with a module support for almost all functionality.</li>\n<li>Open Source: So, you can contribute to the community, the functions you have developed for future use and to help others</li>\n<li>A lot of good interpreters and notebooks available for better experience like jupyter notebook.</li>\n</ol>\n<h4>Cons</h4>\n<ol>\n<li>Being open source, many different ways have developed over the year for same function. This sometimes, creates chaos for others to read someone else code.</li>\n<li>It is a slow language. So, a very bad language to use for developing general algorithms.</li>\n</ol>\n<h2>Documentation</h2>\n<p><a href='https://docs.python.org/3/' target='_blank' rel='nofollow'>Python is well documented</a>. These docs include tutorials, guides, references and meta information for language.</p>\n<p>Another important reference is the Python Enhancement Proposals (<a href='https://www.python.org/dev/peps/' target='_blank' rel='nofollow'>PEPs</a>). Included in the PEPs is a style guide for writing Python code, <a href='https://www.python.org/dev/peps/pep-0008/' target='_blank' rel='nofollow'><code>PEP 8</code></a>.</p>\n<h2>Debugging</h2>\n<p>Inline <code>print</code> statements can be used for simple debugging:</p>\n<blockquote>\n<p><strong>… often the quickest way to debug a program is to add a few print statements to the source: the fast edit-test-debug cycle makes this simple approach very effective.</strong></p>\n<p>—<a href='https://www.python.org/doc/essays/blurb/' target='_blank' rel='nofollow'>Executive Summary</a></p>\n</blockquote>\n<p>Python also includes more powerful tools for debugging, such as:</p>\n<ul>\n<li>logging module, <a href='https://docs.python.org/3/library/logging.html' target='_blank' rel='nofollow'><em>logging</em></a></li>\n<li>debugging module, <a href='https://docs.python.org/3/library/pdb.html' target='_blank' rel='nofollow'><em>pdb</em></a></li>\n</ul>\n<p>Just note that these exist for now.</p>\n<h2>Hello World!</h2>\n<p>Going back to the docs, we can read about the <a href='https://docs.python.org/3/library/functions.html#print' target='_blank' rel='nofollow'><code>print</code></a> function, a <a href='https://docs.python.org/3/library/functions.html' target='_blank' rel='nofollow'><em>built-in function</em></a> of the <a href='https://docs.python.org/3/library/index.html' target='_blank' rel='nofollow'>Python Standard Library</a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>print(*objects, sep=' ', end='\\n', file=sys.stdout, flush=False)</code></pre>\n      </div>\n<p>The built-in functions are listed in alphabetical order. The name is followed by a parenthesized list of formal parameters with optional default values. Under that is a short description of the function and its parameters are given and occasionally an example.</p>\n<p>The <a href='https://docs.python.org/3/library/functions.html#print' target='_blank' rel='nofollow'><code>print</code></a> function in Python 3 replaces the <a href='https://docs.python.org/2/reference/simple_stmts.html#print' target='_blank' rel='nofollow'><code>print</code></a> statement in Python 2.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>>>> print(\"Hello world!\")\nHello world!</code></pre>\n      </div>\n<p>A function is called when the name of the function is followed by <code>()</code>. For the Hello world! example, the print function is called with a string as an argument for the first parameter. For the rest of the parameters the defaults are used.</p>\n<p>The argument that we called the <code>print</code> function with is a <code>str</code> object or <em>string</em>, one of Python’s <a href='https://docs.python.org/3/library/stdtypes.html#text-sequence-type-str' target='_blank' rel='nofollow'><em>built-in types</em></a>.</p>\n<p>The <code>objects</code> parameter is prefixed with a <code>*</code> which indicates that the function will take an arbitrary number of arguments for that parameter.</p>\n<h2>What to learn more?</h2>\n<p>Free Code Camp has some great resources. The web is a big place, there’s plenty more to explore:</p>\n<ul>\n<li>Python Practice Book: <a href=\"http://anandology.com/python-practice-book/index.html\">http://anandology.com/python-practice-book/index.html</a></li>\n<li>Think Python: <a href=\"http://greenteapress.com/thinkpython/html/index.html\">http://greenteapress.com/thinkpython/html/index.html</a></li>\n<li>Practical Business Python: <a href=\"http://pbpython.com/\">http://pbpython.com/</a></li>\n<li>Another course: <a href=\"https://realpython.com/?utm_source=fsp&#x26;utm_medium=promo&#x26;utm_campaign=bestresources\">https://realpython.com/?utm<em>source=fsp&#x26;utm</em>medium=promo&#x26;utm_campaign=bestresources</a></li>\n<li>General: <a href=\"https://www.fullstackpython.com/\">https://www.fullstackpython.com/</a></li>\n<li>Learn the Basics: <a href=\"https://www.codecademy.com/learn/learn-python\">https://www.codecademy.com/learn/learn-python</a></li>\n<li>Computer science using Python: <a href=\"https://www.edx.org/course/introduction-computer-science-mitx-6-00-1x-11?ref=hackernoon#!\">https://www.edx.org/course/introduction-computer-science-mitx-6-00-1x-11?ref=hackernoon#!</a></li>\n<li>List of more resources for learning python: <a href=\"https://github.com/vinta/awesome-python\">https://github.com/vinta/awesome-python</a></li>\n<li>Interactive Python: <a href=\"http://interactivepython.org/runestone/static/thinkcspy/index.html\">http://interactivepython.org/runestone/static/thinkcspy/index.html</a></li>\n<li>Developer’s Guide to Python: <a href=\"https://devguide.python.org/\">https://devguide.python.org/</a></li>\n</ul>","fields":{"slug":"/python/"},"frontmatter":{"title":"Python"}}},"pathContext":{"slug":"/python/"}}

